import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  Card,
  CardContent,
  CardMedia,
  Button,
  Chip,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider,
  IconButton,
  Menu,
  MenuItem,
  Rating,
  LinearProgress,
  Stack
} from '@mui/material';
import {
  BookOnline as BookingIcon,
  Favorite as FavoriteIcon,
  Search as SearchIcon,
  History as HistoryIcon,
  LocationOn as LocationIcon,
  School as SchoolIcon,
  MoreVert as MoreVertIcon,
  Star as StarIcon,
  AccessTime as TimeIcon,
  CheckCircle as CheckCircleIcon,
  Cancel as CancelIcon,
  Schedule as ScheduleIcon
} from '@mui/icons-material';
import { alpha, useTheme } from '@mui/material/styles';
import { motion } from 'framer-motion';
import { useAuth } from '../../context/AuthContext';

const MotionBox = motion(Box);
const MotionPaper = motion(Paper);
const MotionCard = motion(Card);

const fadeInUp = {
  hidden: { opacity: 0, y: 24 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.6, ease: [0.16, 1, 0.3, 1] } }
};

const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.08,
      delayChildren: 0.15
    }
  }
};

const StudentDashboard = () => {
  const theme = useTheme();
  const { user } = useAuth();
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedBooking, setSelectedBooking] = useState(null);
  const [dashboardData, setDashboardData] = useState({
    stats: {
      activeBookings: 0,
      completedBookings: 0,
      favoriteHostels: 0,
      searchesThisMonth: 0
    },
    currentBooking: null,
    recentBookings: [],
    recommendedHostels: [],
    favoriteHostels: []
  });

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      setDashboardData({
        stats: {
          activeBookings: 2,
          completedBookings: 5,
          favoriteHostels: 8,
          searchesThisMonth: 24
        },
        currentBooking: {
          id: 1,
          hostelName: 'Green Valley Hostel',
          checkIn: '2024-11-01',
          checkOut: '2025-03-31',
          rent: 8000,
          status: 'confirmed',
          image: '/api/placeholder/300/200',
          location: 'North Campus, Delhi',
          progress: 68
        },
        recentBookings: [
          {
            id: 1,
            hostelName: 'Green Valley Hostel',
            checkIn: '2024-11-01',
            amount: 8000,
            status: 'confirmed',
            image: '/api/placeholder/150/100'
          },
          {
            id: 2,
            hostelName: 'City Center PG',
            checkIn: '2024-10-15',
            amount: 12000,
            status: 'pending',
            image: '/api/placeholder/150/100'
          },
          {
            id: 3,
            hostelName: 'Student Haven',
            checkIn: '2024-09-01',
            amount: 9500,
            status: 'completed',
            image: '/api/placeholder/150/100'
          }
        ],
        recommendedHostels: [
          {
            id: 1,
            name: 'Modern Student Lodge',
            image: '/api/placeholder/300/200',
            rating: 4.6,
            price: 10000,
            distance: '0.8 km',
            university: 'Delhi University',
            amenities: ['WiFi', 'AC', 'Mess', 'Laundry'],
            trending: true
          },
          {
            id: 2,
            name: 'Elite PG for Boys',
            image: '/api/placeholder/300/200',
            rating: 4.3,
            price: 11500,
            distance: '1.2 km',
            university: 'Delhi University',
            amenities: ['WiFi', 'Gym', 'Parking', 'Security'],
            trending: false
          },
          {
            id: 3,
            name: 'Comfort Zone Hostel',
            image: '/api/placeholder/300/200',
            rating: 4.4,
            price: 9200,
            distance: '0.6 km',
            university: 'Delhi University',
            amenities: ['WiFi', 'Mess', 'Study Room', 'Recreation'],
            trending: true
          }
        ],
        favoriteHostels: [
          { id: 1, name: 'Green Valley Hostel', rating: 4.5, saved: '2024-10-01' },
          { id: 2, name: 'Urban Living PG', rating: 4.2, saved: '2024-09-28' },
          { id: 3, name: 'Student Paradise', rating: 4.7, saved: '2024-09-25' }
        ]
      });
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    }
  };

  const handleMenuClick = (event, booking) => {
    setAnchorEl(event.currentTarget);
    setSelectedBooking(booking);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    setSelectedBooking(null);
  };

  const handleMenuAction = (action) => {
    if (selectedBooking) {
      console.info(`[StudentDashboard] ${action} booking`, selectedBooking.id);
    }
    handleMenuClose();
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'confirmed':
        return 'success';
      case 'pending':
        return 'warning';
      case 'completed':
        return 'info';
      case 'cancelled':
        return 'error';
      default:
        return 'default';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'confirmed':
        return <CheckCircleIcon />;
      case 'pending':
        return <ScheduleIcon />;
      case 'completed':
        return <CheckCircleIcon />;
      case 'cancelled':
        return <CancelIcon />;
      default:
        return <ScheduleIcon />;
    }
  };

  const statCards = [
    {
      title: 'Active Bookings',
      value: dashboardData.stats.activeBookings,
      icon: <BookingIcon sx={{ fontSize: 36, color: 'primary.light' }} />,
      gradient: `linear-gradient(135deg, ${alpha(theme.palette.primary.main, 0.3)} 0%, ${alpha(
        theme.palette.background.default,
        0.78
      )} 100%)`,
      trend: '+12.4%',
      progress: 68,
      description: 'Currently active stays'
    },
    {
      title: 'Completed Stays',
      value: dashboardData.stats.completedBookings,
      icon: <CheckCircleIcon sx={{ fontSize: 36, color: 'success.light' }} />,
      gradient: `linear-gradient(135deg, ${alpha(theme.palette.success.main, 0.32)} 0%, ${alpha(
        theme.palette.background.default,
        0.75
      )} 100%)`,
      trend: '+5.1%',
      progress: 82,
      description: 'Lifetime completed stays'
    },
    {
      title: 'Saved Hostels',
      value: dashboardData.stats.favoriteHostels,
      icon: <FavoriteIcon sx={{ fontSize: 36, color: 'error.light' }} />,
      gradient: `linear-gradient(135deg, ${alpha(theme.palette.error.main, 0.28)} 0%, ${alpha(
        theme.palette.background.default,
        0.78
      )} 100%)`,
      trend: '+18.9%',
      progress: 54,
      description: 'Pinned for quick access'
    },
    {
      title: 'Searches this month',
      value: dashboardData.stats.searchesThisMonth,
      icon: <SearchIcon sx={{ fontSize: 36, color: 'info.light' }} />,
      gradient: `linear-gradient(135deg, ${alpha(theme.palette.info.main, 0.28)} 0%, ${alpha(
        theme.palette.background.default,
        0.8
      )} 100%)`,
      trend: '+32.7%',
      progress: 74,
      description: 'Discovery activity this month'
    }
  ];

  const quickActions = [
    {
      label: 'Search Hostels',
      description: 'Discover curated matches',
      icon: <SearchIcon />, 
      variant: 'contained'
    },
    {
      label: 'View Favorites',
      description: 'Revisit saved properties',
      icon: <FavoriteIcon />, 
      variant: 'outlined'
    },
    {
      label: 'Booking History',
      description: 'Track previous stays',
      icon: <HistoryIcon />, 
      variant: 'outlined'
    },
    {
      label: 'Update Profile',
      description: 'Improve AI recommendations',
      icon: <SchoolIcon />, 
      variant: 'outlined'
    }
  ];

  return (
    <MotionBox
      variants={containerVariants}
      initial="hidden"
      animate="visible"
      sx={{
        minHeight: '100%',
        py: { xs: 3, md: 4 },
        background: `radial-gradient(circle at 0% 0%, ${alpha(
          theme.palette.primary.main,
          0.22
        )} 0%, transparent 45%), radial-gradient(circle at 100% 0%, ${alpha(
          theme.palette.secondary.main,
          0.18
        )} 0%, transparent 55%)`
      }}
    >
      <Container maxWidth="xl">
        <MotionPaper
          variants={fadeInUp}
          sx={{
            mb: 4,
            px: { xs: 3, md: 4 },
            py: { xs: 3, md: 4 },
            borderRadius: 4,
            position: 'relative',
            overflow: 'hidden',
            border: `1px solid ${alpha(theme.palette.primary.main, 0.12)}`,
            background: `linear-gradient(135deg, ${alpha(theme.palette.background.paper, 0.82)} 0%, ${alpha(
              theme.palette.background.default,
              0.9
            )} 100%)`,
            boxShadow: '0 35px 65px -45px rgba(10, 15, 65, 0.95)',
            backdropFilter: 'blur(14px)'
          }}
        >
          <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: 'space-between', gap: 3 }}>
            <Box>
              <Typography variant="overline" color="primary.main" sx={{ letterSpacing: 3, fontWeight: 700 }}>
                Welcome back, {user?.name?.split(' ')[0] || 'Explorer'}
              </Typography>
              <Typography variant="h4" sx={{ fontWeight: 'bold', mt: 1, mb: 1.5 }}>
                Your hostel journey, reimagined
              </Typography>
              <Typography variant="body1" color="text.secondary" sx={{ maxWidth: 520 }}>
                Track upcoming stays, revisit your favourites, and discover hand-picked places that match your vibe.
              </Typography>
            </Box>
            <Stack spacing={1.25} alignItems={{ xs: 'flex-start', md: 'flex-end' }}>
              <Chip
                label="AI-powered recommendations live"
                color="primary"
                variant="outlined"
                sx={{ borderRadius: 2, px: 2, bgcolor: alpha(theme.palette.primary.main, 0.08) }}
              />
              <Typography variant="body2" color="text.secondary">
                Last sync · {new Date().toLocaleTimeString()}
              </Typography>
            </Stack>
          </Box>
        </MotionPaper>

        <Grid container spacing={3} sx={{ mb: 4 }}>
          {statCards.map((stat, index) => (
            <Grid item xs={12} sm={6} md={3} key={stat.title}>
              <MotionPaper
                variants={fadeInUp}
                whileHover={{ y: -6, scale: 1.01 }}
                transition={{ duration: 0.45, ease: [0.16, 1, 0.3, 1] }}
                sx={{
                  height: '100%',
                  p: 3,
                  position: 'relative',
                  overflow: 'hidden',
                  borderRadius: 3,
                  background: stat.gradient,
                  border: `1px solid ${alpha(theme.palette.primary.main, 0.12)}`,
                  boxShadow: '0 28px 65px -40px rgba(15, 23, 42, 0.95)',
                  backdropFilter: 'blur(18px)'
                }}
              >
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                  <Box>
                    <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 0.5 }}>
                      {stat.value}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 0.5 }}>
                      {stat.title}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {stat.description}
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      width: 52,
                      height: 52,
                      borderRadius: '18px',
                      display: 'grid',
                      placeItems: 'center',
                      bgcolor: alpha(theme.palette.common.white, 0.08)
                    }}
                  >
                    {stat.icon}
                  </Box>
                </Box>
                <Stack spacing={1.25} sx={{ mt: 3 }}>
                  <LinearProgress
                    variant="determinate"
                    value={stat.progress}
                    sx={{
                      height: 6,
                      borderRadius: 999,
                      bgcolor: alpha(theme.palette.common.white, 0.08),
                      '& .MuiLinearProgress-bar': {
                        borderRadius: 999,
                        background: `linear-gradient(90deg, ${theme.palette.primary.light} 0%, ${theme.palette.primary.main} 100%)`
                      }
                    }}
                  />
                  <Typography variant="caption" color="text.secondary">
                    {stat.trend} vs last month · {stat.description}
                  </Typography>
                </Stack>
              </MotionPaper>
            </Grid>
          ))}
        </Grid>

        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} md={8}>
            {dashboardData.currentBooking ? (
              <MotionPaper
                variants={fadeInUp}
                whileHover={{ y: -4 }}
                transition={{ duration: 0.5, ease: [0.16, 1, 0.3, 1] }}
                sx={{
                  p: 3,
                  height: '100%',
                  position: 'relative',
                  overflow: 'hidden',
                  borderRadius: 3,
                  border: `1px solid ${alpha(theme.palette.primary.main, 0.12)}`,
                  background: `linear-gradient(135deg, ${alpha(theme.palette.background.paper, 0.8)} 0%, ${alpha(
                    theme.palette.background.default,
                    0.88
                  )} 100%)`,
                  boxShadow: '0 32px 65px -40px rgba(12, 16, 56, 0.95)',
                  backdropFilter: 'blur(18px)'
                }}
              >
                <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                  Current Booking
                </Typography>
                <MotionCard
                  whileHover={{ scale: 1.008 }}
                  transition={{ duration: 0.45, ease: [0.16, 1, 0.3, 1] }}
                  sx={{
                    overflow: 'hidden',
                    borderRadius: 3,
                    border: `1px solid ${alpha(theme.palette.primary.main, 0.06)}`,
                    bgcolor: alpha(theme.palette.background.default, 0.65)
                  }}
                >
                  <Grid container>
                    <Grid item xs={12} md={4}>
                      <CardMedia
                        component="img"
                        height="100%"
                        image={dashboardData.currentBooking.image}
                        alt={dashboardData.currentBooking.hostelName}
                        sx={{
                          minHeight: 220,
                          bgcolor: alpha(theme.palette.primary.main, 0.12)
                        }}
                      />
                    </Grid>
                    <Grid item xs={12} md={8}>
                      <CardContent sx={{ p: { xs: 2.5, md: 3 } }}>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', gap: 2 }}>
                          <Box>
                            <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                              {dashboardData.currentBooking.hostelName}
                            </Typography>
                            <Stack direction="row" spacing={1} alignItems="center" sx={{ mt: 0.75 }}>
                              <LocationIcon sx={{ fontSize: 20, color: 'text.secondary' }} />
                              <Typography variant="body2" color="text.secondary">
                                {dashboardData.currentBooking.location}
                              </Typography>
                            </Stack>
                          </Box>
                          <Chip
                            label={dashboardData.currentBooking.status}
                            color={getStatusColor(dashboardData.currentBooking.status)}
                            icon={getStatusIcon(dashboardData.currentBooking.status)}
                            sx={{ textTransform: 'capitalize', fontWeight: 600 }}
                          />
                        </Box>
                        <Stack spacing={1.25} sx={{ mt: 2 }}>
                          <Stack direction="row" spacing={1.25} alignItems="center">
                            <TimeIcon sx={{ fontSize: 20, color: 'text.secondary' }} />
                            <Typography variant="body2" color="text.secondary">
                              Check-in · {new Date(dashboardData.currentBooking.checkIn).toLocaleDateString()}
                            </Typography>
                          </Stack>
                          <Stack direction="row" spacing={1.25} alignItems="center">
                            <TimeIcon sx={{ fontSize: 20, color: 'text.secondary' }} />
                            <Typography variant="body2" color="text.secondary">
                              Check-out · {new Date(dashboardData.currentBooking.checkOut).toLocaleDateString()}
                            </Typography>
                          </Stack>
                          <Typography variant="h6" color="primary" sx={{ fontWeight: 'bold', mt: 0.5 }}>
                            ₹{dashboardData.currentBooking.rent.toLocaleString()}/month
                          </Typography>
                        </Stack>
                        <Stack spacing={1} sx={{ mt: 3 }}>
                          <LinearProgress
                            variant="determinate"
                            value={dashboardData.currentBooking.progress}
                            sx={{
                              height: 6,
                              borderRadius: 999,
                              bgcolor: alpha(theme.palette.common.white, 0.12),
                              '& .MuiLinearProgress-bar': {
                                borderRadius: 999,
                                background: `linear-gradient(90deg, ${theme.palette.secondary.light} 0%, ${theme.palette.secondary.main} 100%)`
                              }
                            }}
                          />
                          <Typography variant="caption" color="text.secondary">
                            Stay progress · {dashboardData.currentBooking.progress}% completed
                          </Typography>
                        </Stack>
                        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={1.5} sx={{ mt: 3 }}>
                          <Button variant="contained" size="small">
                            View Details
                          </Button>
                          <Button variant="outlined" size="small">
                            Contact Owner
                          </Button>
                        </Stack>
                      </CardContent>
                    </Grid>
                  </Grid>
                </MotionCard>
              </MotionPaper>
            ) : (
              <MotionPaper
                variants={fadeInUp}
                sx={{
                  p: 4,
                  textAlign: 'center',
                  borderRadius: 3,
                  border: `1px dashed ${alpha(theme.palette.primary.main, 0.2)}`,
                  bgcolor: alpha(theme.palette.background.paper, 0.6)
                }}
              >
                <BookingIcon sx={{ fontSize: 60, color: 'grey.400', mb: 2 }} />
                <Typography variant="h6" color="text.secondary" sx={{ mb: 2 }}>
                  No Active Bookings
                </Typography>
                <Button variant="contained" startIcon={<SearchIcon />}>
                  Find Hostels
                </Button>
              </MotionPaper>
            )}
          </Grid>

          <Grid item xs={12} md={4}>
            <MotionPaper
              variants={fadeInUp}
              sx={{
                p: 3,
                height: '100%',
                borderRadius: 3,
                border: `1px solid ${alpha(theme.palette.primary.main, 0.1)}`,
                background: alpha(theme.palette.background.paper, 0.75),
                backdropFilter: 'blur(16px)'
              }}
            >
              <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                Quick Actions
              </Typography>
              <Stack spacing={1.5}>
                {quickActions.map((action) => (
                  <Button
                    key={action.label}
                    variant={action.variant}
                    startIcon={action.icon}
                    fullWidth
                    sx={{
                      justifyContent: 'space-between',
                      py: 1.5,
                      px: 2,
                      borderRadius: 2,
                      textTransform: 'none'
                    }}
                  >
                    <Box sx={{ textAlign: 'left' }}>
                      <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                        {action.label}
                      </Typography>
                      <Typography variant="caption" color="text.secondary">
                        {action.description}
                      </Typography>
                    </Box>
                  </Button>
                ))}
              </Stack>
            </MotionPaper>
          </Grid>
        </Grid>

        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <MotionPaper
              variants={fadeInUp}
              sx={{
                p: 3,
                height: 400,
                overflow: 'auto',
                borderRadius: 3,
                border: `1px solid ${alpha(theme.palette.primary.main, 0.08)}`,
                background: alpha(theme.palette.background.paper, 0.7),
                backdropFilter: 'blur(18px)'
              }}
            >
              <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                Recent Bookings
              </Typography>
              <List>
                {dashboardData.recentBookings.map((booking, index) => (
                  <React.Fragment key={booking.id}>
                    <ListItem
                      secondaryAction={
                        <IconButton edge="end" onClick={(e) => handleMenuClick(e, booking)}>
                          <MoreVertIcon />
                        </IconButton>
                      }
                    >
                      <ListItemIcon>
                        <Box
                          component="img"
                          src={booking.image}
                          alt={booking.hostelName}
                          sx={{
                            width: 60,
                            height: 40,
                            borderRadius: 1,
                            bgcolor: alpha(theme.palette.primary.main, 0.18)
                          }}
                        />
                      </ListItemIcon>
                      <ListItemText
                        primary={
                          <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                            {booking.hostelName}
                          </Typography>
                        }
                        secondary={
                          <Stack spacing={0.75}>
                            <Stack direction="row" spacing={1} alignItems="center">
                              <TimeIcon sx={{ fontSize: 18, color: 'text.secondary' }} />
                              <Typography variant="body2" color="text.secondary">
                                Check-in · {new Date(booking.checkIn).toLocaleDateString()}
                              </Typography>
                            </Stack>
                            <Stack direction="row" justifyContent="space-between" alignItems="center">
                              <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                                ₹{booking.amount.toLocaleString()}
                              </Typography>
                              <Chip
                                label={booking.status}
                                size="small"
                                color={getStatusColor(booking.status)}
                                sx={{ textTransform: 'capitalize' }}
                              />
                            </Stack>
                          </Stack>
                        }
                      />
                    </ListItem>
                    {index < dashboardData.recentBookings.length - 1 && <Divider component="li" sx={{ borderColor: alpha(theme.palette.primary.main, 0.05) }} />}
                  </React.Fragment>
                ))}
              </List>
            </MotionPaper>
          </Grid>

          <Grid item xs={12} md={6}>
            <MotionPaper
              variants={fadeInUp}
              sx={{
                p: 3,
                height: 400,
                overflow: 'auto',
                borderRadius: 3,
                border: `1px solid ${alpha(theme.palette.primary.main, 0.08)}`,
                background: alpha(theme.palette.background.paper, 0.7),
                backdropFilter: 'blur(18px)'
              }}
            >
              <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                Recommended for You
              </Typography>
              <Grid container spacing={2}>
                {dashboardData.recommendedHostels.slice(0, 3).map((hostel) => (
                  <Grid item xs={12} key={hostel.id}>
                    <MotionCard
                      whileHover={{ y: -4 }}
                      transition={{ duration: 0.45, ease: [0.16, 1, 0.3, 1] }}
                      sx={{ display: 'flex', height: 140, borderRadius: 3, border: `1px solid ${alpha(theme.palette.primary.main, 0.06)}` }}
                    >
                      <CardMedia component="img" sx={{ width: 140 }} image={hostel.image} alt={hostel.name} />
                      <CardContent sx={{ flex: 1, p: 2.25 }}>
                        <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 1 }}>
                          <Typography variant="subtitle1" sx={{ fontWeight: 'bold' }}>
                            {hostel.name}
                          </Typography>
                          {hostel.trending && (
                            <Chip
                              icon={<StarIcon sx={{ fontSize: 16 }} />}
                              label="Trending"
                              size="small"
                              color="secondary"
                              sx={{ borderRadius: 1.5 }}
                            />
                          )}
                        </Stack>
                        <Stack direction="row" spacing={1} alignItems="center" sx={{ mb: 1 }}>
                          <Rating value={hostel.rating} precision={0.1} readOnly size="small" />
                          <Typography variant="body2" color="text.secondary">
                            {hostel.rating.toFixed(1)}
                          </Typography>
                        </Stack>
                        <Stack direction="row" spacing={1} alignItems="center" sx={{ mb: 1 }}>
                          <LocationIcon sx={{ fontSize: 18, color: 'text.secondary' }} />
                          <Typography variant="body2" color="text.secondary">
                            {hostel.distance} · {hostel.university}
                          </Typography>
                        </Stack>
                        <Stack direction="row" justifyContent="space-between" alignItems="center">
                          <Typography variant="body2" color="text.secondary">
                            from ₹{hostel.price.toLocaleString()} / month
                          </Typography>
                          <Button size="small" variant="outlined">
                            View
                          </Button>
                        </Stack>
                      </CardContent>
                    </MotionCard>
                  </Grid>
                ))}
              </Grid>
            </MotionPaper>
          </Grid>
        </Grid>

        <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleMenuClose}>
          {selectedBooking && (
            <MenuItem disabled sx={{ opacity: 0.7, fontWeight: 600 }}>
              Manage {selectedBooking.hostelName}
            </MenuItem>
          )}
          {selectedBooking && <Divider sx={{ my: 0.5 }} />}
          <MenuItem onClick={() => handleMenuAction('view')}>
            View Details
          </MenuItem>
          <MenuItem onClick={() => handleMenuAction('download')}>
            Download Receipt
          </MenuItem>
          <MenuItem onClick={() => handleMenuAction('support')}>
            Contact Support
          </MenuItem>
        </Menu>
      </Container>
    </MotionBox>
  );
};

export default StudentDashboard;