import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  Card,
  CardContent,
  Button,
  Chip,
  Avatar,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider,
  IconButton,
  Menu,
  MenuItem
} from '@mui/material';
import {
  TrendingUp as TrendingUpIcon,
  People as PeopleIcon,
  Business as BusinessIcon,
  Assessment as AssessmentIcon,
  MoreVert as MoreVertIcon,
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  Visibility as VisibilityIcon
} from '@mui/icons-material';
import { useAuth } from '../../context/AuthContext';

const AdminDashboard = () => {
  const { user } = useAuth();
  const [anchorEl, setAnchorEl] = useState(null);
  const [stats, setStats] = useState({
    totalUsers: 0,
    totalHostels: 0,
    totalBookings: 0,
    totalRevenue: 0,
    recentUsers: [],
    recentHostels: [],
    recentBookings: []
  });

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      // Simulate API calls - replace with actual API calls
      setStats({
        totalUsers: 1250,
        totalHostels: 320,
        totalBookings: 2840,
        totalRevenue: 1425000,
        recentUsers: [
          { id: 1, name: 'John Doe', email: 'john@example.com', role: 'student', createdAt: '2024-10-15' },
          { id: 2, name: 'Jane Smith', email: 'jane@example.com', role: 'hostel_owner', createdAt: '2024-10-14' },
          { id: 3, name: 'Bob Wilson', email: 'bob@example.com', role: 'student', createdAt: '2024-10-13' }
        ],
        recentHostels: [
          { id: 1, name: 'Green Valley Hostel', owner: 'Ram Kumar', status: 'active', createdAt: '2024-10-12' },
          { id: 2, name: 'City Center PG', owner: 'Priya Sharma', status: 'pending', createdAt: '2024-10-11' },
          { id: 3, name: 'Student Haven', owner: 'Amit Singh', status: 'active', createdAt: '2024-10-10' }
        ],
        recentBookings: [
          { id: 1, student: 'Alice Brown', hostel: 'Green Valley', amount: 8000, status: 'confirmed' },
          { id: 2, student: 'Charlie Davis', hostel: 'City Center PG', amount: 12000, status: 'pending' },
          { id: 3, student: 'Diana Wilson', hostel: 'Student Haven', amount: 9500, status: 'confirmed' }
        ]
      });
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    }
  };

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'active':
      case 'confirmed':
        return 'success';
      case 'pending':
        return 'warning';
      case 'rejected':
      case 'cancelled':
        return 'error';
      default:
        return 'default';
    }
  };

  const statCards = [
    {
      title: 'Total Users',
      value: stats.totalUsers.toLocaleString(),
      icon: <PeopleIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      color: 'primary.light',
      change: '+12%'
    },
    {
      title: 'Total Hostels',
      value: stats.totalHostels.toLocaleString(),
      icon: <BusinessIcon sx={{ fontSize: 40, color: 'secondary.main' }} />,
      color: 'secondary.light',
      change: '+8%'
    },
    {
      title: 'Total Bookings',
      value: stats.totalBookings.toLocaleString(),
      icon: <AssessmentIcon sx={{ fontSize: 40, color: 'success.main' }} />,
      color: 'success.light',
      change: '+24%'
    },
    {
      title: 'Revenue (₹)',
      value: (stats.totalRevenue / 100000).toFixed(1) + 'L',
      icon: <TrendingUpIcon sx={{ fontSize: 40, color: 'warning.main' }} />,
      color: 'warning.light',
      change: '+18%'
    }
  ];

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/* Header */}
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 1 }}>
          Admin Dashboard
        </Typography>
        <Typography variant="body1" color="text.secondary">
          Welcome back, {user?.name}! Here's what's happening with HABS today.
        </Typography>
      </Box>

      {/* Stats Cards */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        {statCards.map((stat, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card
              sx={{
                height: '100%',
                background: `linear-gradient(135deg, ${stat.color} 0%, white 100%)`,
                border: '1px solid',
                borderColor: 'grey.200'
              }}
            >
              <CardContent>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Box>
                    <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 0.5 }}>
                      {stat.value}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {stat.title}
                    </Typography>
                    <Chip
                      label={stat.change}
                      size="small"
                      color="success"
                      sx={{ mt: 1 }}
                    />
                  </Box>
                  <Box>
                    {stat.icon}
                  </Box>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      <Grid container spacing={3}>
        {/* Recent Users */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '400px', overflow: 'auto' }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                Recent Users
              </Typography>
              <Button startIcon={<AddIcon />} size="small">
                Add User
              </Button>
            </Box>
            <List>
              {stats.recentUsers.map((user, index) => (
                <React.Fragment key={user.id}>
                  <ListItem
                    secondaryAction={
                      <IconButton
                        edge="end"
                        onClick={handleMenuClick}
                      >
                        <MoreVertIcon />
                      </IconButton>
                    }
                  >
                    <ListItemIcon>
                      <Avatar sx={{ bgcolor: 'primary.main' }}>
                        {user.name.charAt(0)}
                      </Avatar>
                    </ListItemIcon>
                    <ListItemText
                      primary={user.name}
                      secondary={
                        <Box>
                          <Typography variant="body2" color="text.secondary">
                            {user.email}
                          </Typography>
                          <Chip
                            label={user.role}
                            size="small"
                            variant="outlined"
                            sx={{ mt: 0.5 }}
                          />
                        </Box>
                      }
                    />
                  </ListItem>
                  {index < stats.recentUsers.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </List>
          </Paper>
        </Grid>

        {/* Recent Hostels */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '400px', overflow: 'auto' }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                Recent Hostels
              </Typography>
              <Button startIcon={<AddIcon />} size="small">
                Manage
              </Button>
            </Box>
            <List>
              {stats.recentHostels.map((hostel, index) => (
                <React.Fragment key={hostel.id}>
                  <ListItem
                    secondaryAction={
                      <IconButton
                        edge="end"
                        onClick={handleMenuClick}
                      >
                        <MoreVertIcon />
                      </IconButton>
                    }
                  >
                    <ListItemText
                      primary={hostel.name}
                      secondary={
                        <Box>
                          <Typography variant="body2" color="text.secondary">
                            Owner: {hostel.owner}
                          </Typography>
                          <Chip
                            label={hostel.status}
                            size="small"
                            color={getStatusColor(hostel.status)}
                            sx={{ mt: 0.5 }}
                          />
                        </Box>
                      }
                    />
                  </ListItem>
                  {index < stats.recentHostels.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </List>
          </Paper>
        </Grid>

        {/* Recent Bookings */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '400px', overflow: 'auto' }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                Recent Bookings
              </Typography>
              <Button startIcon={<AssessmentIcon />} size="small">
                View All
              </Button>
            </Box>
            <List>
              {stats.recentBookings.map((booking, index) => (
                <React.Fragment key={booking.id}>
                  <ListItem
                    secondaryAction={
                      <IconButton
                        edge="end"
                        onClick={handleMenuClick}
                      >
                        <MoreVertIcon />
                      </IconButton>
                    }
                  >
                    <ListItemText
                      primary={`${booking.student} → ${booking.hostel}`}
                      secondary={
                        <Box>
                          <Typography variant="body2" color="text.secondary">
                            ₹{booking.amount.toLocaleString()}
                          </Typography>
                          <Chip
                            label={booking.status}
                            size="small"
                            color={getStatusColor(booking.status)}
                            sx={{ mt: 0.5 }}
                          />
                        </Box>
                      }
                    />
                  </ListItem>
                  {index < stats.recentBookings.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </List>
          </Paper>
        </Grid>
      </Grid>

      {/* Context Menu */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={handleMenuClose}>
          <VisibilityIcon sx={{ mr: 1 }} />
          View Details
        </MenuItem>
        <MenuItem onClick={handleMenuClose}>
          <EditIcon sx={{ mr: 1 }} />
          Edit
        </MenuItem>
        <MenuItem onClick={handleMenuClose} sx={{ color: 'error.main' }}>
          <DeleteIcon sx={{ mr: 1 }} />
          Delete
        </MenuItem>
      </Menu>
    </Container>
  );
};

export default AdminDashboard;