import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  Card,
  CardContent,
  CardMedia,
  Button,
  Chip,
  List,
  ListItem,
  ListItemText,
  Divider,
  IconButton,
  Menu,
  MenuItem,
  Rating,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField
} from '@mui/material';
import {
  Business as BusinessIcon,
  TrendingUp as TrendingUpIcon,
  People as PeopleIcon,
  MonetizationOn as MoneyIcon,
  Visibility as VisibilityIcon,
  Edit as EditIcon,
  Add as AddIcon,
  MoreVert as MoreVertIcon,
  Home as HomeIcon,
  CheckCircle as CheckCircleIcon,
  Schedule as ScheduleIcon,
  Cancel as CancelIcon
} from '@mui/icons-material';
import { useAuth } from '../../context/AuthContext';

const OwnerDashboard = () => {
  const { user } = useAuth();
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedItem, setSelectedItem] = useState(null);
  const [addHostelDialog, setAddHostelDialog] = useState(false);
  const [dashboardData, setDashboardData] = useState({
    stats: {
      totalHostels: 0,
      totalBookings: 0,
      monthlyRevenue: 0,
      occupancyRate: 0
    },
    hostels: [],
    recentBookings: [],
    pendingRequests: []
  });

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      // Simulate API call - replace with actual API calls
      setDashboardData({
        stats: {
          totalHostels: 3,
          totalBookings: 45,
          monthlyRevenue: 185000,
          occupancyRate: 78
        },
        hostels: [
          {
            id: 1,
            name: 'Green Valley Hostel',
            image: '/api/placeholder/300/200',
            rating: 4.5,
            totalRooms: 20,
            occupiedRooms: 16,
            monthlyRevenue: 128000,
            status: 'active',
            location: 'Near Delhi University'
          },
          {
            id: 2,
            name: 'City Center PG',
            image: '/api/placeholder/300/200',
            rating: 4.2,
            totalRooms: 15,
            occupiedRooms: 12,
            monthlyRevenue: 180000,
            status: 'active',
            location: 'Near Mumbai University'
          },
          {
            id: 3,
            name: 'Student Haven',
            image: '/api/placeholder/300/200',
            rating: 4.7,
            totalRooms: 25,
            occupiedRooms: 18,
            monthlyRevenue: 171000,
            status: 'pending',
            location: 'Near Bangalore University'
          }
        ],
        recentBookings: [
          {
            id: 1,
            studentName: 'Rahul Sharma',
            hostelName: 'Green Valley Hostel',
            checkIn: '2024-11-01',
            amount: 8000,
            status: 'confirmed',
            phone: '+91 9876543210'
          },
          {
            id: 2,
            studentName: 'Priya Singh',
            hostelName: 'City Center PG',
            checkIn: '2024-10-28',
            amount: 12000,
            status: 'pending',
            phone: '+91 9876543211'
          },
          {
            id: 3,
            studentName: 'Amit Kumar',
            hostelName: 'Student Haven',
            checkIn: '2024-10-25',
            amount: 9500,
            status: 'confirmed',
            phone: '+91 9876543212'
          }
        ],
        pendingRequests: [
          { id: 1, studentName: 'Neha Gupta', hostelName: 'Green Valley Hostel', requestDate: '2024-10-16' },
          { id: 2, studentName: 'Vikash Patel', hostelName: 'City Center PG', requestDate: '2024-10-15' },
          { id: 3, studentName: 'Anjali Reddy', hostelName: 'Student Haven', requestDate: '2024-10-14' }
        ]
      });
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    }
  };

  const handleMenuClick = (event, item, type) => {
    setAnchorEl(event.currentTarget);
    setSelectedItem({ ...item, type });
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    setSelectedItem(null);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'active':
      case 'confirmed':
        return 'success';
      case 'pending':
        return 'warning';
      case 'cancelled':
      case 'inactive':
        return 'error';
      default:
        return 'default';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'confirmed':
      case 'active':
        return <CheckCircleIcon />;
      case 'pending':
        return <ScheduleIcon />;
      case 'cancelled':
      case 'inactive':
        return <CancelIcon />;
      default:
        return <ScheduleIcon />;
    }
  };

  const statCards = [
    {
      title: 'Total Hostels',
      value: dashboardData.stats.totalHostels,
      icon: <HomeIcon sx={{ fontSize: 40, color: 'primary.main' }} />,
      color: 'primary.light',
      description: 'Properties listed'
    },
    {
      title: 'Total Bookings',
      value: dashboardData.stats.totalBookings,
      icon: <PeopleIcon sx={{ fontSize: 40, color: 'secondary.main' }} />,
      color: 'secondary.light',
      description: 'All time bookings'
    },
    {
      title: 'Monthly Revenue',
      value: `₹${(dashboardData.stats.monthlyRevenue / 1000).toFixed(0)}K`,
      icon: <MoneyIcon sx={{ fontSize: 40, color: 'success.main' }} />,
      color: 'success.light',
      description: 'This month'
    },
    {
      title: 'Occupancy Rate',
      value: `${dashboardData.stats.occupancyRate}%`,
      icon: <TrendingUpIcon sx={{ fontSize: 40, color: 'info.main' }} />,
      color: 'info.light',
      description: 'Current occupancy'
    }
  ];

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/* Header */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Box>
          <Typography variant="h4" sx={{ fontWeight: 'bold', mb: 1 }}>
            Owner Dashboard
          </Typography>
          <Typography variant="body1" color="text.secondary">
            Welcome back, {user?.name}! Manage your hostels and bookings.
          </Typography>
        </Box>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => setAddHostelDialog(true)}
          sx={{ px: 3 }}
        >
          Add New Hostel
        </Button>
      </Box>

      {/* Stats Cards */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        {statCards.map((stat, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card
              sx={{
                height: '100%',
                background: `linear-gradient(135deg, ${stat.color} 0%, white 100%)`,
                border: '1px solid',
                borderColor: 'grey.200'
              }}
            >
              <CardContent>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Box>
                    <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 0.5 }}>
                      {stat.value}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 0.5 }}>
                      {stat.title}
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {stat.description}
                    </Typography>
                  </Box>
                  <Box>
                    {stat.icon}
                  </Box>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* My Hostels */}
      <Paper sx={{ p: 3, mb: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
          <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
            My Hostels
          </Typography>
          <Button variant="outlined" size="small">
            View All
          </Button>
        </Box>
        <Grid container spacing={3}>
          {dashboardData.hostels.map((hostel) => (
            <Grid item xs={12} md={4} key={hostel.id}>
              <Card
                sx={{
                  height: '100%',
                  position: 'relative',
                  transition: 'transform 0.2s',
                  '&:hover': { transform: 'translateY(-2px)' }
                }}
              >
                <CardMedia
                  component="img"
                  height="180"
                  image={hostel.image}
                  alt={hostel.name}
                  sx={{ bgcolor: 'grey.300' }}
                />
                <Chip
                  label={hostel.status}
                  color={getStatusColor(hostel.status)}
                  icon={getStatusIcon(hostel.status)}
                  sx={{
                    position: 'absolute',
                    top: 8,
                    right: 8
                  }}
                />
                <CardContent>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 1 }}>
                    <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                      {hostel.name}
                    </Typography>
                    <IconButton
                      size="small"
                      onClick={(e) => handleMenuClick(e, hostel, 'hostel')}
                    >
                      <MoreVertIcon />
                    </IconButton>
                  </Box>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                    <Rating value={hostel.rating} precision={0.1} readOnly size="small" />
                    <Typography variant="body2" sx={{ ml: 1 }}>
                      {hostel.rating}
                    </Typography>
                  </Box>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                    {hostel.location}
                  </Typography>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                    <Box>
                      <Typography variant="caption" color="text.secondary">
                        Occupancy
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                        {hostel.occupiedRooms}/{hostel.totalRooms} rooms
                      </Typography>
                    </Box>
                    <Box sx={{ textAlign: 'right' }}>
                      <Typography variant="caption" color="text.secondary">
                        Monthly Revenue
                      </Typography>
                      <Typography variant="body2" sx={{ fontWeight: 'bold', color: 'success.main' }}>
                        ₹{(hostel.monthlyRevenue / 1000).toFixed(0)}K
                      </Typography>
                    </Box>
                  </Box>
                  <Box sx={{ display: 'flex', gap: 1 }}>
                    <Button variant="contained" size="small" fullWidth>
                      Manage
                    </Button>
                    <Button variant="outlined" size="small" fullWidth>
                      View
                    </Button>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Paper>

      {/* Recent Bookings & Pending Requests */}
      <Grid container spacing={3}>
        {/* Recent Bookings */}
        <Grid item xs={12} md={8}>
          <Paper sx={{ p: 3, height: '400px', overflow: 'auto' }}>
            <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
              Recent Bookings
            </Typography>
            <List>
              {dashboardData.recentBookings.map((booking, index) => (
                <React.Fragment key={booking.id}>
                  <ListItem
                    secondaryAction={
                      <IconButton
                        edge="end"
                        onClick={(e) => handleMenuClick(e, booking, 'booking')}
                      >
                        <MoreVertIcon />
                      </IconButton>
                    }
                  >
                    <ListItemText
                      primary={`${booking.studentName} → ${booking.hostelName}`}
                      secondary={
                        <Box>
                          <Typography variant="body2" color="text.secondary">
                            Check-in: {new Date(booking.checkIn).toLocaleDateString()}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            Phone: {booking.phone}
                          </Typography>
                          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 0.5 }}>
                            <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                              ₹{booking.amount.toLocaleString()}
                            </Typography>
                            <Chip
                              label={booking.status}
                              size="small"
                              color={getStatusColor(booking.status)}
                            />
                          </Box>
                        </Box>
                      }
                    />
                  </ListItem>
                  {index < dashboardData.recentBookings.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </List>
          </Paper>
        </Grid>

        {/* Pending Requests */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, height: '400px', overflow: 'auto' }}>
            <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
              Pending Requests
            </Typography>
            <List>
              {dashboardData.pendingRequests.map((request, index) => (
                <React.Fragment key={request.id}>
                  <ListItem sx={{ px: 0 }}>
                    <ListItemText
                      primary={request.studentName}
                      secondary={
                        <Box>
                          <Typography variant="body2" color="text.secondary">
                            {request.hostelName}
                          </Typography>
                          <Typography variant="caption" color="text.secondary">
                            {new Date(request.requestDate).toLocaleDateString()}
                          </Typography>
                          <Box sx={{ mt: 1, display: 'flex', gap: 1 }}>
                            <Button variant="contained" size="small" color="success">
                              Accept
                            </Button>
                            <Button variant="outlined" size="small" color="error">
                              Reject
                            </Button>
                          </Box>
                        </Box>
                      }
                    />
                  </ListItem>
                  {index < dashboardData.pendingRequests.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </List>
          </Paper>
        </Grid>
      </Grid>

      {/* Context Menu */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={handleMenuClose}>
          <VisibilityIcon sx={{ mr: 1 }} />
          View Details
        </MenuItem>
        <MenuItem onClick={handleMenuClose}>
          <EditIcon sx={{ mr: 1 }} />
          Edit
        </MenuItem>
        {selectedItem?.type === 'booking' && (
          <MenuItem onClick={handleMenuClose}>
            <BusinessIcon sx={{ mr: 1 }} />
            Contact Student
          </MenuItem>
        )}
      </Menu>

      {/* Add Hostel Dialog */}
      <Dialog open={addHostelDialog} onClose={() => setAddHostelDialog(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Add New Hostel</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Hostel Name"
            fullWidth
            variant="outlined"
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Location"
            fullWidth
            variant="outlined"
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Total Rooms"
            type="number"
            fullWidth
            variant="outlined"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setAddHostelDialog(false)}>Cancel</Button>
          <Button variant="contained">Add Hostel</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default OwnerDashboard;